.fotter-outer-container{
background-color: #111;
color: gray;
display: flex;
justify-content: center;
font-size: 15px;

}
.fotter-inner-container{
 width: 68%;
}
.fotter-icons ul{
width: 12%;
display: flex;
justify-content: space-between;
margin-left: 10px;
list-style: none;
position: relative;
left: -10px;
top: 5px;
}
.fotter-icons ul li{
    color: white;
}
.fotter-data{
    display: flex;
    justify-content: space-between;
}

.fotter-data ul {
    list-style: none;
    display: flex;
    justify-content: space-around;
    flex-direction: column;
    height: 12vh;
}
.service-code{
border: #fff 1px solid;
width: 10%;
margin: 10px 0;
cursor: pointer;
}
.copy-right{
 font-size: 12px;
 margin-bottom: 10px;
}